import jquery = require("jquery");
import {AButton} from "./abstract/a-button";
/** 
 * @classdesc Componente responsável por renderizar um RadioButton
 * 
 * @example
 * 
 * let radioButton:RadioButton = new RadioButton("Feminino","F");
 * radioButton.setLabel("Sexo: ");
 * 
 * @export
 * @extends {AButton}
 * @param {string} label Rótulo do RadioButton
 * @param {(string|number)} value Valor do RadioButton
 * @memberof module:mvcomponents/button
 */
export class RadioButton extends AButton {
	constructor(label: string, value: string | number) {
		super("label", `<input value="${value}" type="radio" style="cursor:pointer"><span>${label}</span>`);
		jquery(this.element)
			.addClass("radio-inline RadioButton");
		jquery(this.element)
			.find("span:first")
			.css({cursor:"default"})
			.on("click",function (evt:Event){
				evt.stopPropagation();
				evt.preventDefault();
			});
	}
	/**
	 * Atribui ou substitui o rótulo do RadioButton.
	 * 
	 * @example
	 * 
	 * let radioButton:RadioButton = new RadioButton("Feminino","F");
	 * radioButton.setLabel("Sexo: ");
	 * 
	 * @param icon {string}
	 * @return {RadioButton}
	 * 
	 */
	public setLabel(label: string): this {
		jquery(this.element)
			.find("span:first")
			.text(label);
		return this;
	}
	/**
	 * Atribui ou substitui o nome do input do RadioButton.
	 * 
	 * @example
	 * 
	 * let radioButton:RadioButton = new RadioButton("Feminino","F");
	 * radioButton.setName("sexo");
	 * 
	 * @param name {string}
	 * @return {RadioButton}
	 * 
	 */
	public setName(name: string): this {
		jquery(this.element)
			.find("input")
			.attr("name", name);
		return this;
	}
	/**
	 * Desabilita ou habilita o RadioButton.
	 * 
	 * @example
	 * 
	 * let radioButton:RadioButton = new RadioButton("Feminino","F");
	 * radioButton.setDisabled(false);
	 * 
	 * @param disabled {boolean}
	 * @return {RadioButton}
	 */
	public setDisabled(disabled: boolean): this {
		jquery(this.element)
			.find("input:first")
			.prop("disabled", disabled);
		return this;
	}
	/**
	 * Verifica se o Radiobutton está desabilitado.
	 * 
	 * @example
	 * 
	 * let radioButton:RadioButton = new RadioButton("Feminino","F");
	 * radioButton.isDisabled();
	 * 
	 * @return {boolean}
	 */
	public isDisabled():boolean {
		return jquery(this.element)
				.find("input:first")
				.prop("disabled");
	}
	/**
	 * Verifica se o RadioButton está checado.
	 * 
	 * @example
	 * 
	 * let radioButton:RadioButton = new RadioButton("Feminino","F");
	 * radioButton.isChecked();
	 * 
	 * @return {boolean}
	 */
	public isChecked():boolean {
		return jquery(this.element)
					.find("input:first")
					.is(":checked");
	}
	/**
	 * Atribui ou substitui o Tooltip do RadioButton.
	 * 
	 * @example
	 * 
	 * let radioButton:RadioButton = new RadioButton("Feminino","F");
	 * radioButton.setTooltip('Tooltip do RadioButton');
	 * 
	 * @param tooltip {string}
	 * @return {RadioButton}
	 */
	public setTooltip(tooltip: string): this {
		if ( tooltip ) {
			jquery(this.element)
				.attr("title", tooltip);
		} else {
			jquery(this.element)
				.removeAttr("title");
		}
		return this;
	}
}
